export default {
  //
  getCode(telphone){
    return axios.post(
      'user/sendVerCode',
      qs.stringify({
        phone:telphone,
      })
    )
  },

  //
  userLogin(data){
    return axios.post(
      'user/login',
      qs.stringify({
        password:data.password,
        type:data.type, // 1: 2
        loginName:data.loginName,
        redirectType:data.redirectType,
      })
    )
  },

//   
  checkOut(){
    return axios.post(
      'user/logout',
      qs.stringify({})//
    )
  },


  //   
  houseCompared(data){
    // return axios.get('house/houseCompare?data='+data)
    return axios.get('house/houseCompare',{
      params:{
        data:data
      }
    })
  },


  //
  getRentHouseList(){
    return axios.get('house/getRentHouseList',{

    })
  },

  //
  getDistrict(data){
    return axios.post(
      'house/getRegion',
      qs.stringify({
        city:data.city,
      })
    )
  },

  //
  getFilterList(data){
    return axios.post(
      'house/getFilterList',
      qs.stringify({
        type:data.type,//1: 2
      })
    )
  },
  //
  getSellHouseList(data){
    return axios.get('house/getHouseList',{
      params:{
        cityId:data.cityId,
        communityId:data.communityId,
        areaIds:data.areaIds,
        priceMin:data.priceMin,
        priceMax:data.priceMax,
        filterIds:data.filterIds.join(','),
        pageSize:data.pageSize,
        pageIndex:data.pageIndex,
        orderBy:data.orderBy,
        orderColumn:data.orderColumn,
      }
    })
  },

  //
  getRentHouseList(data){
    return axios.get('house/getRentHouseList',{
      params:{
        cityId:data.cityId,
        communityId:data.communityId,
        areaIds:data.areaIds,
        priceMin:data.priceMin,
        priceMax:data.priceMax,
        filterIds:data.filterIds.join(','),
        pageSize:data.pageSize,
        pageIndex:data.pageIndex,
        orderBy:data.orderBy,
        orderColumn:data.orderColumn,
      }
    })
  },

  //
  getHouseDetail(data){
    return axios.get('house/getHouseDetailStr',{
      params:{
        cityId:data.cityId,
        houseId:data.houseId,
        userType:data.userType,
        houseType:data.houseType,
      }
    })
  },

  //
  getCommunityDetail(data){
    return axios.get('house/getCommunityDetailStr',{
      params:{
        blockId:data.blockId,
        city:data.city,
        userType:data.userType,
        houseType:data.houseType,
      }
    })
  },

  //
  searchCommunity(data){
    return axios.post('house/searchCommunity', qs.stringify({
      keyword:data.keyword,//()
      city:data.city,//(hz)
      limit:data.limit,//5
    }))
  }
}



